import React from 'react';
import classes from './Cocktail.module.css';
import png from '../../../assets/cocktail2.png';

const Cocktail = () => {
    return (

        <div className={classes.Cocktail}>
            <div className={classes.Container}>
                <h2>Margarita</h2>
                <img src={png}/>
            </div>
        </div>

    )
}


export default Cocktail;